<template>
    <div class="item-manage">
        <adt-search-folder
            :searchRouter="{query: {label: search.label, examineStatus: search.examineStatus, useStatus: search.useStatus}}"
            :isStatus="true">
            <template slot="status">
                <div class="status">
                    <Col span="10">
                        <div class="name">审核状态:</div>
                        <span v-for="status in auditStatus" :key="status.dictionaryKey" @click="chooseExamineStatus(status.dictionaryKey)" :class="{'click-status': search.examineStatus === status.dictionaryKey}">
                            {{ status.dictionaryValue }}
                        </span>
                    </Col>
                    <Col span="14">
                        <div class="name">启用状态:</div>
                        <span v-for="status in enableStatus" :key="status.dictionaryKey" @click="chooseUseStatus(status.dictionaryKey)" :class="{'click-status': search.useStatus === status.dictionaryKey}">
                            {{ status.dictionaryValue }}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <Col span="8" class="form-item">
                    <span>数据项名称:</span>
                    <Input v-model="search.label" clearable />
                </Col>
            </template>
        </adt-search-folder>
        <div class="content">
            <div class="btns-paginator">
                <adt-button>
                    <Button @click="add()">新增数据项<Icon type="ios-add-circle-outline" /></Button>
                    <Button @click="startUsing()">{{ '批量启用' + (isManager ? '' : '申请') }}<Icon type="ios-add-circle-outline" /></Button>
                    <Button @click="stopUsing()">
                        {{ '批量停用' + (isManager ? '' : '申请') }}
                        <svg class="iconfont" aria-hidden="true">
                          <use xlink:href="#icon-tingyong" />
                        </svg>
                    </Button>
                </adt-button>
                <adt-paginator :currentPage="~~page" :total="~~total" />
            </div>
            <div class="public-table-data">
                <Table :columns="columns" :data="tableData" @on-selection-change="selectionChange" stripe>
                    <template #createDatetime="{ row }">
                        {{ timestampFormat(row.createDatetime) }}
                    </template>
                    <template #action="{ row }">
                        <!-- 审核状态为待审核（wait），不显示启用停用按钮 -->
                        <span class="icon-action icon-action-word" @click="startUsing(row)" v-if="
                        (!row.useStatus || row.useStatus.trim() === '已停用') && row.examineStatus !== '待审核'">启</span>
                        <span class="icon-action icon-action-word" @click="stopUsing(row)"  v-if="(row.useStatus && row.useStatus.trim() === '已启用') && row.examineStatus !== '待审核'">停</span>
                        <Icon class="icon-action" type="ios-contact-outline" @click="examine(row)" v-if="isManager && row.examineStatus === '待审核'" />
                        <!-- <Icon class="icon-action" type="md-create" @click="edit(row)" /> -->
                        <!-- <Icon class="icon-action" type="md-close" @click="remove(row)" /> -->
                        <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
                    </template>
                </Table>
            </div>
        </div>
        <!-- 新增数据项modal -->
        <adt-my-modal
            :title="modalTitle"
            okText="保存"
            :showModal="isModalShow"
            @on-visible-change="addVisibleChange"
            @on-ok="save()"
            @on-cancel="cancel()"
            width="650">
            <Form
                ref="formItemData"
                inline
                :model="formItem"
                :rules="formItemRule"
                :label-width="120"
                label-position="left"
                label-colon
                class="two-column"
                >
                <FormItem label="数据项编号" prop="name">
                    <Input v-model="formItem.name" :disabled="!isAdd" clearable></Input>
                </FormItem>
                <FormItem label="数据项名称" prop="label">
                    <Input v-model="formItem.label" clearable></Input>
                </FormItem>
                <!-- <FormItem label="语言编码" prop="languageCode">
                    <Input v-model="formItem.languageCode"></Input>
                </FormItem> -->
                <FormItem label="数据项分类" prop="itemType">
                    <Select v-model="formItem.itemType" clearable>
                        <Option v-for="item in itemType" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                    </Select>
                </FormItem>
                <FormItem label="控件类型" prop="controlType">
                    <Select v-model="formItem.controlType" clearable>
                        <Option v-for="item in controlType" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                    </Select>
                </FormItem>
                <FormItem label="数据类型" prop="dataType">
                    <Select v-model="formItem.dataType" clearable>
                        <Option v-for="item in dataType" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                    </Select>
                </FormItem>
                <FormItem label="数据最大长度" prop="maxLength">
                    <Input v-model="formItem.maxLength" clearable></Input>
                </FormItem>
                <FormItem label="数据精度" prop="accuracy" :class="formItem.dataType === 'string' || !formItem.dataType ? '' : 'ivu-form-item-required'" :show-message="formItem.dataType === 'string' || !formItem.dataType ? false : true">
                    <Input v-model="formItem.accuracy" clearable></Input>
                </FormItem>
                <FormItem label="数据来源" prop="source" :class="formItem.controlType === 'I' || !formItem.controlType ? '' : 'ivu-form-item-required'" :show-message="formItem.controlType === 'I' || !formItem.controlType ? false : true">
                    <Select v-model="formItem.source" clearable>
                        <Option v-for="item in dataSource" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                    </Select>
                </FormItem>
                <FormItem label="数据来源值" prop="sourceValue" :class="formItem.controlType === 'I' || !formItem.controlType ? '' : 'ivu-form-item-required'" :show-message="formItem.controlType === 'I' || !formItem.controlType ? false : true">
                    <Input v-model="formItem.sourceValue" clearable></Input>
                    <!-- <Select v-model="formItem.sourceValue">
                        <Option v-for="item in dataSourceValue" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                    </Select> -->
                </FormItem>
                <FormItem label="备注规则" prop="note">
                    <Input type="textarea" v-model="formItem.note" style="width: 470px;"></Input>
                </FormItem>
            </Form>
        </adt-my-modal>
        <!-- 审核modal -->
        <adt-my-modal
            :title="examineModalTitle"
            :showModal="isExamineModalShow"
            @on-visible-change="examineVisibleChange"
            @on-ok="examineSave()"
            width="600">
            <!-- <Row>
                <Col span="8">
                    数据项编号：{{ examineInfo.name }}
                </Col>
                <Col span="8">
                    操作人：GD53453
                </Col>
                <Col span="8">
                    操作时间：GD53453
                </Col>
            </Row> -->
            <RadioGroup v-model="agree" style="margin-bottom: 20px;">
                <Radio label="1">同意{{ examineType === 1 ? '启用' : '停用' }}</Radio>
                <Radio label="0" style="margin-left: 30px;">不同意{{ examineType === 1 ? '启用' : '停用' }}</Radio>
            </RadioGroup>
            <div v-show="agree === '0'">
                <div class="reason-name" style="margin-bottom: 6px;">填写驳回原因:</div>
                <Input type="textarea" v-model.trim="reason" placeholder="请输入内容..." />
            </div>
        </adt-my-modal>
        <adt-reconfirm-modal :showModal="isActionDel" :message="reconfirmMsg" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import {
    apiTemplateDataQuery,
    apiTemplateDataAdd,
    apiTemplateDataEnable,
    apiTemplateDataDisable,
    apiTemplateDataEnableExamPass,
    apiTemplateDataEnableExamFail,
    apiTemplateDataDisableExamPass,
    apiTemplateDataDisableExamFail
} from '@/api/dataItemManage'
import { apiDictionaryQueryByCacheTypeKey } from '@/api/dictionary'
import paginator from '@/mixins/paginator'
import validateRules from '@/libs/validateRules'
import { timestampFormat } from '@/libs/utils'
export default {
    name: 'ItemManage',
    mixins: [paginator],
    props: {
        label: '',
        examineStatus: '',
        useStatus: ''
    },
    data () {
        return {
            enableStatus: this.$getDictionary('ENABLE_STATUS'),
            auditStatus: this.$getDictionary('AUDIT_STATUS'),
            itemType: this.$getDictionary('ITEM_TYPE'),
            controlType: this.$getDictionary('CONTROL_TYPE'),
            dataType: this.$getDictionary('DATA_TYPE'),
            dataSource: this.$getDictionary('DATA_SOURCE'),
            search: {
                label: this.label,
                examineStatus: this.examineStatus,
                useStatus: this.useStatus
            },
            // 是否管理员
            isManager: this.$route.query.isManager,
            // 审核类型：{启用: 1, 停用: 0}
            examineType: 1,
            modalTitle: '新增数据项',
            examineModalTitle: '启用审核',
            isModalShow: false,
            isExamineModalShow: false,
            isActionDel: false,
            reconfirmMsg: '确定删除吗',
            // 审核信息
            examineInfo: {},
            // 新增form信息
            formItem: {
                name: '',
                label: '',
                itemType: '',
                controlType: '',
                dataType: '',
                maxLength: '',
                accuracy: '',
                source: '',
                sourceValue: '',
                note: ''
            },
            tableData: [],
            columns: [
                {
                    title: '选择',
                    align: 'center',
                    type: 'selection',
                    width: 60
                },
                {
                    title: '数据项编号',
                    align: 'center',
                    key: 'name'
                },
                {
                    title: '数据项名称',
                    align: 'center',
                    key: 'label'
                },
                {
                    title: '控件类型',
                    align: 'center',
                    key: 'controlType'
                },
                {
                    title: '数据来源',
                    align: 'center',
                    key: 'source'
                },
                {
                    title: '数据来源值',
                    align: 'center',
                    key: 'sourceValue'
                },
                {
                    title: '数据类型',
                    align: 'center',
                    key: 'dataType'
                },
                {
                    title: '创建人',
                    align: 'center',
                    key: 'createBy'
                },
                {
                    title: '创建时间',
                    align: 'center',
                    slot: 'createDatetime'
                },
                {
                    title: '审核状态',
                    align: 'center',
                    key: 'examineStatus'
                },
                {
                    title: '启用状态',
                    align: 'center',
                    key: 'useStatus'
                },
                {
                    title: '操作',
                    align: 'center',
                    slot: 'action',
                    width: 150
                }
            ],
            formItemRule: {
                name: [
                    validateRules.required('数据项编号')[0],
                    {pattern: /^[a-z_]*$/, message: '只能输入小写字母和_'}
                ],
                label: [
                    validateRules.required('数据项名称')[0]
                    // {pattern: /^[\u4E00-\u9FA5]+$/, message: '只能输入中文'}
                ],
                itemType: [
                    validateRules.select('数据项分类')[0]
                ],
                controlType: [
                    validateRules.select('控件类型')[0]
                ],
                dataType: [
                    validateRules.select('数据类型')[0]
                ],
                maxLength: [
                    validateRules.required('数据最大长度')[0],
                    {pattern: /^[0-9]*$/, message: '只能输入数字'}
                ]
            },
            // 审核同意、不同意
            agree: '1',
            // 不同意驳回原因
            reason: '',
            // 新增编辑区分  true为新增 false为编辑
            isAdd: true,
            deleteItemInfo: {},
            // 选中的值
            selectionList: []
        }
    },
    methods: {
        // 格式化时间
        timestampFormat (timeStamp) {
            return timestampFormat(timeStamp)
        },
        chooseExamineStatus (id) {
            if (this.search.examineStatus === id) {
                this.search.examineStatus = ''
            } else {
                this.search.examineStatus = id
            }
        },
        chooseUseStatus (id) {
            if (this.search.useStatus === id) {
                this.search.useStatus = ''
            } else {
                this.search.useStatus = id
            }
        },
        selectionChange (selectionList) {
            this.selectionList = selectionList
        },
        addVisibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        examineVisibleChange (changeStatus) {
            this.isExamineModalShow = changeStatus
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        // 点击搜索调用查询接口
        dataInit () {
            this.getData()
        },
        getData () {
            const data = {
                page: this.page,
                ...this.search
            }
            apiTemplateDataQuery({ data }).then(res => {
                this.tableData = res.data.content || []
                this.total = res.data.totalElements
            })
        },
        // 新增数据项
        add () {
            this.isModalShow = true
            this.modalTitle = '新增数据项'
            this.isAdd = true
        },
        startUsing (dataSource) {
            const data = this.handleRequestData(dataSource)
            // 批量操作，没有选择数据项
            if (!data.length) return
            apiTemplateDataEnable({ data }).then(res => {
                this.$Message.info(res.message)
                this.getData()
            })
        },
        stopUsing (dataSource) {
            const data = this.handleRequestData(dataSource)
            // 批量操作，没有选择数据项
            if (!data.length) return
            apiTemplateDataDisable({ data }).then(res => {
                this.$Message.info(res.message)
                this.getData()
            })
        },
        // 将数据处理成ajax请求需要的入参数据
        handleRequestData (data) {
            const requestData = []
            // 单个启用、停用
            if (data) {
                requestData.push(handle(data))
                return requestData
            } else {
                // 批量启用、停用
                if (!this.selectionList.length) {
                    this.$Message.warning('请先选择数据项')
                } else {
                    for (const list of this.selectionList) {
                        requestData.push(handle(list))
                    }
                }
                return requestData
            }
            function handle (obj) {
                const { name, useStatus, version, examineStatus } = obj
                return {
                    name,
                    useStatus,
                    version,
                    examineStatus
                }
            }
        },
        // 修改数据项
        edit (row) {
            this.isAdd = false
            const params = {name: row.name}
            getTemplateDataQuery(params).then(res => {
                this.formItem = res.data
                this.formItem.maxLength = this.formItem.maxLength.toString()
                // this.languageArray = res.data.labelAndLanguage[0].label
                // 编辑时语言栏展示数据处理
                // let arr = []
                // res.data.labelAndLanguage.forEach(item => {
                //     arr.push(item.il8nLangCode)
                // })
                // this.formItem.nameAndLang = arr
                // this.formItem.nameAndLang = res.data.labelAndLanguage[0].label
                // 当后台返回的校验规则为空时赋值[]
                this.formItem.rules = res.data.rules || []
                this.isModalShow = true
                this.modalTitle = '编辑数据项'
            })
        },
        remove (row) {
            this.isActionDel = true
            this.deleteItemInfo = {name: row.name}
        },
        detail (row) {
            this.$router.push(`/standard-mapper/item-manage/detail/${row.name}`)
        },
        allDelClick () {
            apiTemplateDataDel(this.deleteItemInfo).then(res => {
                if (res.code == 200) {
                    this.$Message.info("删除成功")
                    // 隐藏模态框
                    this.isActionDel = false
                    // 刷新列表
                    this.getData()
                } else {
                    this.$Message.info("删除失败")
                }
            })
        },
        save () {
            const vm = this
            // 控件类型不为输入框，数据来源吧和数据来源值是必填的
            if (this.formItem.controlType !== 'I') {
                this.formItemRule.source = validateRules.select('数据来源')
                this.formItemRule.sourceValue = validateRules.select('数据来源值')
            } else {
                this.formItemRule.source = []
                this.formItemRule.sourceValue = []
            }
            // 数据类型为字符串，数据精度非必填项
            if (this.formItem.dataType !== 'string') {
                this.formItemRule.accuracy = validateRules.required('数据精度')
            } else {
                this.formItemRule.accuracy = []
            }
            this.$refs.formItemData.validate((valid) => {
                if (valid) {
                    const data = this.formItem
                    // 新增保存
                    if (this.isAdd) {
                        apiTemplateDataAdd({ data }).then(res => {
                            successCallback()
                            this.getData()
                        })
                    } else {
                        // 编辑保存
                        apiTemplateDataUpdate({ data }).then(res => {
                            successCallback()
                        })
                    }
                }
            })
            function successCallback () {
                vm.$Message.info("保存成功")
                // 重置表单数据
                vm.$refs.formItemData.resetFields()
                // 隐藏新增模态框
                vm.isModalShow = false
                // 刷新列表
                vm.getData()
            }
        },
        cancel () {
            this.$refs.formItemData.resetFields()
        },
        examine (row) {
            // 根据状态判断是启用审核
            if (row.useStatus !== 'enable') {
                this.examineType = 1
                this.examineModalTitle = '启用审核'
            } else {
                this.examineType = 0
                this.examineModalTitle = '停用审核'
            }
            this.examineInfo.name = row.name
            this.examineInfo.version = row.version
            this.isExamineModalShow = true
        },
        examineSave () {
            const data = { ...this.examineInfo}
            // 启用审核
            if (this.examineType === 1) {
                // 同意
                if (this.agree === '1') {
                    apiTemplateDataEnableExamPass({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isExamineModalShow = false
                        this.getData()
                    })
                } else {
                    if (!this.reason) {
                        this.$Message.warning('请填写驳回原因！')
                        return
                    }
                    data.message = this.reason
                    // 不同意
                    apiTemplateDataEnableExamFail({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isExamineModalShow = false
                        this.getData()
                    })
                }
            } else {
                // 停用审核
                // 同意
                if (this.agree === '1') {
                    apiTemplateDataDisableExamPass({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isExamineModalShow = false
                        this.getData()
                    })
                } else {
                    if (!this.reason) {
                        this.$Message.warning('请填写输入驳回原因！')
                        return
                    }
                    data.message = this.reason
                    // 不同意
                    apiTemplateDataDisableExamFail({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isExamineModalShow = false
                        this.getData()
                    })
                }
            }
        }
    }
}
</script>
